import { Component } from 'react';
import getContent from './backend.js'
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import './tabela.css';
import { Table, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

class ListarAlunos extends Component {
    state = {
        alunos: [],
    }
    async componentDidMount() {
        const response = await getContent.get('/aluno2')
        console.log(response);
        this.setState({ alunos: response.data })
    };
    render() {
        const { alunos } = this.state;
        return (
            <div className="tabela">
                <Table striped bordered hover variant="dark">
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th>Nome</th>
                            <th>Telefone</th>
                            <th>Status</th>
                        </tr>
                    </thead>
                    <tbody>
                        {alunos.map(aluno2 => (
                            <tr key={aluno2.id}>
                                <td>{aluno2.id}</td>
                                <td>{aluno2.nome}</td>
                                <td>{aluno2.telefone}</td>
                                <td>{aluno2.status}</td>
                            </tr>
                        ))}
                    </tbody>
                </Table>
                <Link to="/"><Button className="w-100 btn btn-lg btn-primary">Voltar</Button></Link>
            </div>
        )
    }
};

export default ListarAlunos;