import React from 'react';
import {BrowserRouter, Switch, Route} from 'react-router-dom';
import ListarAlunos from './ListarAlunos.js';
import InserirAluno from './InserirAluno.js';
import ExcluirAluno from './ExcluirAluno.js';
import AlterarAluno from './AlterarAluno.js';
import BuscarID from './BuscarID.js';
import Page from './Page.js';

function Routes (props){
    return(
        <BrowserRouter>
            <Switch>
                <Route path="/" exact component={Page} />
                <Route path="/listaralunos" component={ListarAlunos} />
                <Route path="/inseriraluno" component={InserirAluno} />
                <Route path="/excluiraluno" component={ExcluirAluno} />
                <Route path="/alteraraluno" component={AlterarAluno} />
                <Route path="/buscarid" component={BuscarID} />
            </Switch>
        </BrowserRouter>
    )
}

export default Routes;