import { Component } from 'react';
import logo from './logo.svg';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import './signin.css';
import { Form, Button, Image } from 'react-bootstrap';
import getContent from './backend.js';
import { Link } from 'react-router-dom';

class InserirAluno extends Component {
    constructor(props) {
        super(props)
        this.state = {
            nome: '',
            telefone: '',
            status: ''
        }
    }
    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }
    submitHandler = e => {
        e.preventDefault()
        console.log(this.state)
        getContent.post('/aluno2', this.state)
            .then(response => {
                console.log(response)
            })
            .catch(error => {
                console.log(error)
            })
    };
    render() {
        const { nome, telefone, status } = this.state
        return (
            <div>
                <Form onSubmit={this.submitHandler}>
                    <Image className="mb-4 App-logo" src={logo} alt="" width="72" height="57" />
                    <h1 className="h5 mb-5 fw-normal">Digite os Dados do Aluno</h1>

                    <Form.Group className="form-floating">
                        <Form.Control type="text" id="floatingInput" name="nome" placeholder="Nome Do Aluno" value={nome} onChange={this.changeHandler} />
                        <Form.Label >Nome do Aluno</Form.Label>
                    </Form.Group>

                    <Form.Group className="form-floating">
                        <Form.Control type="text" id="floatingPassword" name="telefone" placeholder="99999999999" value={telefone} onChange={this.changeHandler} />
                        <Form.Label >Telefone</Form.Label>
                    </Form.Group>

                    <Form.Group className="form-floating">
                        <Form.Control type="text" id="floatingPassword" name="status" placeholder="ATIVO/INATIVO" value={status} onChange={this.changeHandler} />
                        <Form.Label >Status</Form.Label>
                    </Form.Group>

                    <Button className="w-100 btn btn-lg btn-primary" type="submit">Inserir</Button>
                    <Link to="/"><Button className="w-100 btn btn-lg btn-primary">Voltar</Button></Link>
                    <p className="mt-5 mb-3 text-muted">&copy; 2017–2021</p>
                </Form>
            </div>
        )
    }
}

export default InserirAluno;