import { Component } from 'react';
import logo from './logo.svg';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import './signin.css';
import { Form, Button, Image } from 'react-bootstrap';
import getContent from './backend.js';
import { Link } from 'react-router-dom';

class ExcluirAluno extends Component {
    constructor(props) {
        super(props)
        this.state = {
            id: ''
        }
    }
    changeHandler = e => {
        this.setState({[e.target.name]: e.target.value})
    }
    submitHandler = e => {
        e.preventDefault()
        console.log(this.state)
        getContent.delete('/aluno2/' + this.state.id, this.state)
            .then(response => {
                console.log(response)
            })
            .catch(error => {
                console.log(error)
            })
    };
    render() {
        const {id} = this.state;
        return (
            <div>
                <Form onSubmit={this.submitHandler}>
                <Image className="mb-4 App-logo" src={logo} alt="" width="72" height="57" />
                <h1 className="h5 mb-5 fw-normal">Digite o ID do Aluno que Deseja Excluir</h1>

                <Form.Group className="form-floating">
                    <Form.Control type="number" id="floatingInput" name="id" placeholder="ID Do Aluno" value={id} onChange={this.changeHandler} />
                    <Form.Label >ID do Aluno</Form.Label>
                </Form.Group>
                <Button className="w-100 btn btn-lg btn-primary" type="submit">Excluir</Button>
                <Link to="/"><Button className="w-100 btn btn-lg btn-primary">Voltar</Button></Link>
                <p className="mt-5 mb-3 text-muted">&copy; 2017–2021</p>
                </Form>
            </div>
        )
    }
}

export default ExcluirAluno;