import './App.css';
import { Component } from 'react';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button } from 'react-bootstrap';

class Page extends Component {
    render() {
        return (
            <div>
                <div className="container">
                    <h1><p>Escolha uma opção:</p></h1>
                    <div className="display">
                        <div className="menus">
                            <p>Ver Lista de Alunos</p>
                            <Link to="/listaralunos"><Button className="w-100 btn btn-lg btn-primary" >Excluir</Button></Link>
                        </div>
                        <div className="menus">
                            <p>Inserir Um Aluno</p>
                            <Link to="/inseriraluno"><Button className="w-100 btn btn-lg btn-primary" >Excluir</Button></Link>
                        </div>
                        <div className="menus">
                            <p>Excluir um Aluno</p>
                            <Link to="/excluiraluno"><Button className="w-100 btn btn-lg btn-primary" >Excluir</Button></Link>
                        </div>
                        <div className="menus">
                            <p>Alterar um Aluno</p>
                            <Link to="/alteraraluno"><Button className="w-100 btn btn-lg btn-primary">Excluir</Button></Link>
                        </div>
                        <div className="menus">
                            <p>Buscar Aluno por ID</p>
                            <Link to="/buscarid"><Button className="w-100 btn btn-lg btn-primary">Excluir</Button></Link>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Page;